.App {
  text-align: center;
  margin-top: 50px;
}

.button-container button,
.undo-redo-container button {
  margin: 5px;
  padding: 10px 15px;
  font-size: 14px;
  border-radius: 5px;
  background-color: #4caf50;
  border: none;
  cursor: pointer;
}

.progress-container {
  width: 80%;
  height: 30px;
  background-color: #e0e0e0;
  margin: 20px auto;
  border-radius: 5px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background-color: #4caf50;
  transition: width 0.5s ease-in-out;
}

.undo-redo-container {
  margin-top: 20px;
}


